html, body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
}

* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #ffffff;
  color: var(--primary-text, white);

}

:root {
  --primary-color: #282c34;
  --primary-text: #ffffff;
}
.img-link:hover .photo {
  height: 80%;
}

.img-link:hover .img-description {
  -webkit-transform: scaleY(1);
          transform: scaleY(1);
}

.img-wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
  -webkit-transform: scale(0.4);
          transform: scale(0.4);
  opacity: 0.1;
  margin: 20px 0;
  height: 500px;
  max-height: 90vh;
}

.photo {
  width: 100%;
  height: 90%;
  display: inline-flex;
  object-fit: cover;
  transition: height 0.5s cubic-bezier(0.65, 0.24, 0.35, 1.1);
}
.hide-image {
  visibility: 0;
  -webkit-transform: scale(0);
          transform: scale(0);
}

.caption {
  flex-grow: 1;
  bottom: 0;
  height: 10%;
  background-color: #282c34;
  background-color: var(--primary-color, #282c34);
  color: #fdfdfd;
  padding: 10px;
  font-size: 12px;
}

.img-description {
  -webkit-transform: scaleY(0);
          transform: scaleY(0);
  transition: -webkit-transform 0.5s cubic-bezier(0.65, 0.24, 0.35, 1.1);
  transition: transform 0.5s cubic-bezier(0.65, 0.24, 0.35, 1.1);
  transition: transform 0.5s cubic-bezier(0.65, 0.24, 0.35, 1.1), -webkit-transform 0.5s cubic-bezier(0.65, 0.24, 0.35, 1.1);
}

@-webkit-keyframes boom {
  0% {
    -webkit-transform: scale(0.6) translateY(3.2rem);
            transform: scale(0.6) translateY(3.2rem);
    opacity: 0.3;
  }

  100% {
    -webkit-transform: none;
            transform: none;
    opacity: 1;
  }
}

@keyframes boom {
  0% {
    -webkit-transform: scale(0.6) translateY(3.2rem);
            transform: scale(0.6) translateY(3.2rem);
    opacity: 0.3;
  }

  100% {
    -webkit-transform: none;
            transform: none;
    opacity: 1;
  }
}

.grow {
  -webkit-animation-name: boom;
          animation-name: boom;
  -webkit-animation-duration: 300ms;
          animation-duration: 300ms;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}

.modal {
  z-index: 1000;
  width: 100%;
  height: 100%;
  background-color: rgba(31, 42, 65, 0.8);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  overflow: hidden;
  transition: opacity 0.5s cubic-bezier(0.65, 0.24, 0.35, 1.1),
    -webkit-transform 0.5s cubic-bezier(0.65, 0.24, 0.35, 1.1);
  transition: transform 0.5s cubic-bezier(0.65, 0.24, 0.35, 1.1),
    opacity 0.5s cubic-bezier(0.65, 0.24, 0.35, 1.1);
  transition: transform 0.5s cubic-bezier(0.65, 0.24, 0.35, 1.1),
    opacity 0.5s cubic-bezier(0.65, 0.24, 0.35, 1.1),
    -webkit-transform 0.5s cubic-bezier(0.65, 0.24, 0.35, 1.1);
}

.modal-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 95%;
  height: 100vh;
  position: relative;
}

.modal-img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.close {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background-color: #fdfdfd;
  color: rgb(10, 18, 27);
  border: 2px solid currentColor;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  position: absolute;
  top: 5px;
  right: 0;
  font-weight: bold;
  font-size: 20px;
  text-decoration: none;
}

@media screen and (min-width: 800px) {
  .modal-content {
    width: 70%;
  }
  .close {
    right: -10px;
  }
}

.hide {
  -webkit-transform: scale(0);
          transform: scale(0);
  opacity: 0;
}

.show {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
}

.App,
.App-header,
main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  color: var(--primary-text, white);
}

.App-header {
  height: 10vh;
  background-color: #282c34;
  background-color: var(--primary-color, #282c34);
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 100;
}

.grid {
  flex-grow: 1;
  max-width: 1200px;
  margin-top: 10vh;
  padding: 20px;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-auto-rows: minmax(180px, auto);
  grid-auto-flow: dense;
}

.bottom {
  font-size: 2rem;
  color: var(--primary-color);
}

